html, body, .audio-page {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
}

.audio-page {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/* 版头容器，绝对定位于页面顶部 */
.banner-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 0;  /* 确保在 header 后面 */
}

/* header.js 中的 header 默认在最外层，我们假设其背景为透明 */
.header {
  position: relative;
  z-index: 1;
  background-color: transparent;
}

/* 主内容区域，下移以避开版头区域 */
.audio-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 2;
  margin-top: 250px;  /* 与版头容器高度一致 */
}

/* 中间内容区域 */
.audio-content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
}

/* 左右两侧区域 */
.side {
  flex: 0 0 auto;
}
.side.left {
  margin-right: 20px;
}
.side.right {
  margin-left: 20px;
}

/* 左侧图片样式 */
.side.left img {
  max-height: 100%;
  max-width: 100px;
  object-fit: contain;
}
.side.right img {
  max-height: 100%;
  max-width: 500px;
  object-fit: contain;
}

/* 中间区域：包含音频播放器和资源列表 */
.audio-middle {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* 播放器及标题说明 */
.audio-header {
  text-align: center;
  margin-bottom: 20px;
}

/* 音频列表区域 */
.audio-list {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* 每一行音频项，支持横向滚动 */
.audio-row {
  display: flex;
  gap: 10px;
  overflow-x: auto;
  padding: 10px;
  white-space: nowrap;
}

/* 单个音频项 */
.audio-item {
  min-width: 120px;
  height: 80px;
  background-color: #ddd;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  border-radius: 8px;
}

/* 底部背景容器 */
.banner-bottom {
  position: absolute;
  width: 100%;
  z-index: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 0;  /* 确保在 header 后面 */
}

